* {
  font-family: Arial, Helvetica, sans-serif;
}

li {
  border: 1px solid black;
  list-style: none;
  padding: 1rem;
  width: 20vw;
  display: flex;
  flex-direction: column;
}

i {
  font-size: 1.5rem;
}

h5 {
  padding: 0;
  margin: 0;
}

.buttons {
  padding: 1rem 0 1rem 0;
  display: flex;
  width: 1rem;
  flex-direction: column;
  align-items: center;

  button {
    width: 1rem;
    border: none;
    display: flex;
    justify-content: center;
    outline: none;
    -webkit-outline: none;
    background-color: black;
    color: rgb(250, 250, 250);
    font-weight: bolder;
  }
}
